.contacts {
    flex: 1 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: showOtherPages 1.5s;
    &__wrapper {
        display: flex;
        flex: 1 0 auto;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        text-align: center;
    }
    &__title {
        @include adaptiv-font(60,27);
        font-weight: 600;
        margin-bottom: 10px;
        @media (max-width: 576px) {
            margin-bottom: 5px;
        }
    }
    &__descr {
        @include adaptiv-font(20,13);
        font-weight: 300;
    }
    &__social {
        @media (max-width: 768px) {
            margin-bottom: 15px;
        }
        @media (max-width: 576px) {
            margin-bottom: 5px;
        }
    }
    &__items {
        display: flex;
        justify-content: space-around;
        margin-bottom: 10px;
        @media (max-width: 355px) {
            margin-bottom: 0px;
        }
    }
    &__adress {
        @include adaptiv-font(20,14);
        font-weight: 300;
        span {
            display: block;
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
        a {
            color: var(--color-black);
        }
    }

    &__callback {
        form {
            display: flex;
            flex-direction: column;
        }
        label {
            font-weight: 600;
            @include adaptiv-font(20,14);
            text-align: left;
            margin-bottom: 10px;
            @media (max-width: 355px) {
                margin-bottom: 5px;
            }
        }
        input {
            display: block;
            max-width: 575px;
            width: 100%;
            border: 1px solid #cecece;
            @include adaptiv-font(20,14);
            padding: 16px 10px;
            margin-bottom: 30px;
            @media (max-width: 1540px) {
                margin-bottom: 15px;
                padding:10px;
            }
            @media (max-width: 355px) {
                margin-bottom: 7px;
                padding: 7px;
            }
        }
    }
}
.contacts .contacts__items .social__item {
        margin-bottom: 0;
        @media (max-width: 768px) {
            margin-bottom: 0;
        }
        @media (max-width: 355px) {
            width: 20px;
            height: 20px;
            margin-bottom: 10px;
        }
        svg {
            fill: #000;

        }
    }
