.box {
    background-color: var(--color-white);
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (min-width: 992px) {
        animation: showOtherPages 1.5s;
    }
    &__slider {
        @media (max-width: 992px) {
            display: none;
        }
    }
    &__slider-mob {
        @media (min-width: 992px) {
            display: none;
        }
        @media (max-width: 992px) {
            display: flex;
        }
    }
    &__wrapper {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 0;
        @media (max-width: 992px) {
            justify-content: center;
        }
    }
    &__startup {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        @media (max-width: 992px) {
           background-color: inherit;
           padding: 10px 40px;
           margin-top: 20px;
        }
        @media (max-width: 576px) {
           margin-top: 10px;
           padding: 5px 40px;
        }
    }
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__rights {
        font-size: 13px;
        text-align: center;
        padding: 20px 0 0 0;
        @media (max-width: 1540px) {
            padding: 15px 0 4px 0;
        }
        @media (max-width: 992px) {
            display: none;
        }
    }
    &__content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        z-index: 2;
        text-align: center;
        width: 300px;
        padding: 0 12px;
        max-height: 150px;
        user-select: none;
        overflow: hidden;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.5s ease, visibility 0.5s ease;
        @media (max-width: 1850px) {
            width: 200px;
        }
        @media (max-width: 1100px) {
            width: 160px;
        }
        @media (max-width: 992px) {
            opacity: 1;
            visibility: visible;
            width: 300px;
        }
        @media (max-width: 355px) {
            width: 200px;
        }
    }
    &__text {
        font-size: 35px;
        font-weight: 700;
        margin-bottom: 10px;
        @media (max-width: 1850px) {
            font-size: 25px;
        }
        @media (max-width: 992px) {
            font-size: 35px;
        }
        @media (max-width: 355px) {
            font-size: 25px;
        }
    }
    &__subtext {
        font-size: 18px;
        font-weight: 500;
        @media (max-width: 1850px) {
            font-size: 15px;
        }
        @media (max-width: 992px) {
            font-size: 18px;
        }
        @media (max-width: 355px) {
            font-size: 14px;
        }
    }
}
.startup {
    
    &__text {
        @include adaptiv-font(23, 14);
        font-weight: 300;
        color: var(--color-white);
        margin-bottom: 17px;
        text-align: center;
        width: 100%;
        @media (max-width: 992px) {
           color: var(--color-black);
           font-weight: 700;
        }
        @media (max-width: 576px) {
           margin-bottom: 10px;
        }
    }
    &__btn {
        width: 250px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 600;
        font-size: 20px;
        background-color: var(--color-white);
        border: 4px solid transparent;
        transition: all 0.5s ease;
        @media (max-width: 1850px) {
            width: 180px;
            height: 50px;
            font-size: 18px;
        }
        @media (max-width: 1540px) {
            width: 150px;
            height: 40px;
            font-size: 16px;
            border: 2px solid transparent;
        }
        @media (max-width: 992px) {
            background-color: var(--color-black);
            color:#fff;
        }
        @media (min-width: 992px) {
            &:hover {
                background-color: var(--color-black);
                color:#fff;
                border: 4px solid #fff;
                transition: all 0.5s ease;
                @media (max-width: 1540px) {
                    border: 2px solid #fff;
                }
            }
        }
        
    }
}
