.modal {
    display: none;
    &-acc {
        display: none;
    }
    &__overlay {
        background: rgba(0, 0, 0, 0.85);
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 1000;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: auto;
    }

    &__window {
        background-color: #fff;
        max-width: 690px;
        width: 100%;
        padding: 70px 60px;
        margin: 50px 20px;
        position: relative;
        @media (max-width: 1540px) {
            padding: 30px;
            max-width: 590px;
        }
        @media (max-width: 576px) {
            margin: 10px 15px;
        }
        @media (max-width: 380px) {
            padding: 20px 30px;
            max-width: 590px;
        }
    }
    &__form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        label {
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 10px;
            @media (max-width: 1540px) {
                font-size: 16px;
            }
        }
        input,
        textarea,
        select {
            border: 1px solid #cecece;
            font-size: 18px;
            transition: border 0.3s ease;
            @media (max-width: 1540px) {
                font-size: 16px;
            }
            &:hover {
                border: 1px solid var(--color-black);
                transition: border 0.3s ease;
            }
            &:focus {
                outline: 1px solid rgb(81, 70, 228);
                &::placeholder {
                    opacity: 0;
                    transition: opacity 0.3s ease;
                }
            }
        }
        input {
            padding: 15px 10px;
            margin-bottom: 25px;
            @media (max-width: 1540px) {
                padding: 10px 5px;
            }
            @media (max-width: 380px) {
                padding: 5px 10px;
                margin-bottom: 9px;
            }
            &::placeholder {
                font-size: 16px;
                font-weight: 300;
                opacity: 1;
                transition: opacity 0.3s ease;
                @media (max-width: 370px) {
                    font-size: 14px;
                }
            }
        }
        select {
            padding: 10px;
            margin-bottom: 25px;
        }
        textarea {
            height: 138px;
            resize: none;
            overflow: auto;
            padding: 10px;
            margin-bottom: 30px;
            @media (max-width: 1540px) {
                height: 100px;
                padding: 5px;
            }
            @media (max-width: 370px) {
                margin-bottom: 20px;
                height: 85px;
            }
        }
    }
    &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 20px;
        height: 20px;
        padding: 5px;
        cursor: pointer;
        &::after,
        &::before {
            content: "";
            position: absolute;
            display: block;
            top: 50%;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: var(--color-black);
        }
        &::after {
            transform: rotate(-45deg);
        }
        &::before {
            transform: rotate(45deg);
        }
    }
    .deals__text {
        margin: 20px 0;
    }
}