.nav {
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 992px) {
            justify-content: flex-end;
        }
        &_modified {
            @media (min-width: 992px) {
                animation: moveNavUp 1.5s;
            }
        }
        &_main {
            position: relative;
            z-index: 10;
            @media (min-width: 992px) {
                animation: moveNavDown 1.5s;
            }
        }
    }
    &__text {
        @include adaptiv-font(32, 16);
        @media (min-width: 992px) {
            animation: showOtherPages 1.5s;
        }
        @media (max-width: 992px) {
            display: none;
        }
    }
    &__items {
        @media (max-width: 992px) {
            position: fixed;
            overflow: auto;
            flex-direction: column;
            width: 60%;
            height: 100%;
            top: 0;
            right: -100%;
            background-color: var(--color-black);
            transition: right 0.5s ease;
            padding-bottom: 30px;
            &-active {
                z-index: 20;
                right: 0;
                transition: right 0.5s ease;
            }
        }
    }
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 992px) {
            flex-direction: column;
            justify-content: center;
            align-items: start;
            padding: 35px 0px 0px 20px;
        }
    }
    &__item {
        white-space: nowrap;
        width: 100%;
        &:not(:last-child) {
            margin-right: 240px;
            @media (max-width: 1850px) {
                margin-right: 200px;
            }
            @media (max-width: 1540px) {
                margin-right: 130px;
            }
            @media (max-width: 1100px) {
                margin-right: 100px;
            }
            @media (max-width: 992px) {
                margin-right: 0;
                margin-bottom: 0;
            }
        }
    }
    &__link {
        display: inline-block;
        width: 100%;
        font-weight: 300;
        @include adaptiv-font(30, 18);
        color: var(--color-black);
        position: relative;
        user-select: none;
        cursor: pointer;
        text-transform: uppercase;
        @media (max-width: 1540px) {
            font-size: 23px;
        }
        @media (max-width: 992px) {
            color: var(--color-white);
        }
    }
    &__link_main {
        padding: 1.25em 0;
        @media (max-width: 992px) {
            padding: 15px 0;
        }
        @media (max-width: 576px) {
            padding: 10px 0;
        }
        &::after {
            content: "";
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 0px;
            height: 8px;
            background-color: var(--color-black);
            transition: width 0.5s ease;
            @media (max-width: 992px) {
                display: none;
            }
        }
        &:hover::after {
            width: 100%;
            transition: width 0.5s ease;
        }

        &--active {
            font-weight: 600;
            transform: translateY(-5%);
            &::after {
                width: 100%;
                transform: translateY(80%);
            }
        }
    }
    &__link_mod {
        padding: 20px 0;
        @media (max-width: 576px) {
            padding: 10px 0;
        }
        &::before {
            content: "";
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            width: 0px;
            height: 8px;
            background-color: var(--color-black);
            transition: width 0.5s ease;
            @media (max-width: 992px) {
                display: none;
            }
        }

        &:hover::before {
            width: 100%;
            transition: width 0.5s ease;
        }
        &--active {
            font-weight: 600;
            transform: translateY(-5%);
            &::before {
                width: 100%;
                transform: translateY(50%);
            }
        }
    }
    .social-nav {
        display: none;
        @media (max-width: 992px) {
            display: flex;
            padding: 0 20px;
            justify-content: space-between;
        }
        @media (max-width: 768px) {
            align-items: start;
        }
    }
}
