.project-page {
    display: none;
    &__overlay {
        background: rgba(0, 0, 0, 0.85);
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 1000;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: auto;
        padding: 30px 50px;
        @media (max-width: 992px) {
            padding: 10px;
        }
    }

    &__window {
        background-color: #fff;
        max-width: 1750px;
        width: 100%;
        height: 100%;
        overflow: auto;
        padding: 0px 0px 50px 10px;
        &::-webkit-scrollbar {
            width: 7px;
            height: 100%;
            background-color: var(--color-white);
            position: relative;
        }
        &::-webkit-scrollbar-thumb {
            background-color: var(--color-middlegray);
        }
        @media (max-width: 992px) {
            position: relative;
        }
    }

    &__pagination {
        position: sticky;
        max-width: 150px;
        width: 100%;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        @media (max-width: 1340px) {
            max-width: 140px;
        }
        @media (max-width: 992px) {
            max-width: 80px;
        }
    }
    &__pagination-title {
        @include adaptiv-font(20, 13);
        font-weight: 700;
        margin-bottom: 20px;
        max-width: 190px;
        @media (max-width: 1340px) {
            max-width: 140px;
        }
        @media (max-width: 992px) {
            max-width: 80px;
        }
    }

    &__pagination-item {
        a {
            @include adaptiv-font(20, 10);
            font-weight: 700;
            color: var(--color-middlegray);
            display: inline-block;
            width: 70%;
            span {
                color: var(--color-middlegray);
            }
        }
        &:not(:last-child) {
            margin-bottom: 35px;
            @media (max-width: 992px) {
                margin-bottom: 25px;
            }
        }
        @media (min-width: 992px) {
            &:hover span {
                color: var(--color-black);
                transition: color 0.5s ease;
            }
        }
        &_active {
            a {
                color: var(--color-black);
            }
        }
    }

    &__content {
        padding: 0 3% 0 12%;
        margin-top: -200px;
        @media (max-width: 992px) {
            padding: 0 3% 0 110px;
        }
        @media (max-width: 576px) {
            padding: 0 3% 0 80px;
        }
    }

    &__close {
        position: absolute;
        top: 30px;
        right: 5px;
        z-index: 2000;
        background-color: var(--color-white);
        border-radius: 50%;
        width: 40px;
        height: 40px;
        cursor: pointer;
        &::after,
        &::before {
            content: "";
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            background-color: var(--color-black);
            width: 15px;
            height: 1px;
        }
        &::before {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
        &::after {
            transform: translate(-50%, -50%) rotate(45deg);
        }
        @media (max-width: 992px) {
            position: fixed;
            top: 20px;
            right: auto;
            left: 20px;
        }
    }
    &__btns {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        a {
            display: inline-block;
        }
    }
    &__btn {
        text-align: center;
        
        margin: 10px;
    }
}
.content {
    &__header {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 80px;
        padding-left: 5px;
        @media (max-width: 992px) {
            margin-bottom: 70px;
        }
        @media (max-width: 576px) {
            margin-bottom: 50px;
        }
    }

    &__image {
        max-width: 220px;
        max-height: 220px;
        min-width: 100px;
        margin-bottom: 10px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__project-name {
        @include adaptiv-font(60, 20);
        font-weight: 600;
        padding: 0 5px 0 0;
    }
    &__item {
        position: relative;
        max-width: 600px;
        padding-left: 40px;
        margin-bottom: 100px;
        @media (max-width: 992px) {
            padding-left: 25px;
            margin-bottom: 50px;
        }
        @media (max-width: 576px) {
            padding-left: 20px;
            margin-bottom: 25px;
        }
        &:nth-child(2n) {
            margin-left: auto;
        }
    }

    &__digit {
        position: absolute;
        top: 0;
        left: 0;
        font-weight: 900;
        @include adaptiv-font(24, 14);
        color: #ddd;
    }

    &__title {
        @include adaptiv-font(60, 20);
        font-weight: 700;
        margin-bottom: 10px;
    }

    &__descr {
        @include adaptiv-font(20, 13);
        font-weight: 300;
    }
    &__photos {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        padding-top: 30px;
        max-width: 450px;
    }
    &__photo {
        max-width: 250px;
        margin: 20px 20px 0 0;
        flex: 1 0 auto;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        @media (max-width: 650px) {
            width: 100%;
        }
    }
}
