.iv {
    direction: rtl;
    text-align: left;
}
.box .box__wrapper .box-item {
    max-width: 349px;
    width: 100%;
    min-height: 349px;
    @media (max-width: 1850px) {
        max-width: 260px;
        min-height: 260px;
    }
    @media (max-width: 1340px) {
        max-width: 220px;
        min-height: 220px;
    }
    @media (max-width: 1100px) {
        max-width: 180px;
        min-height: 180px;
    }
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
    &_colored {
        background-color: var(--color-black);
    }
    &__slider {
        position: relative;
        cursor: pointer;
        &::after {
            content: "";
            position: absolute;
            z-index: 1;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.6);
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.5s ease, visibility 0.5s ease;
            @media (max-width: 992px) {
                opacity: 1;
                visibility: visible;
            }
        }
        @media (min-width: 992px) {
            &:hover {
                &::after {
                    opacity: 1;
                    visibility: visible;
                }
                .box__content {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }
    &-mob {
        max-width: 349px;
        width: 100%;
        min-height: 349px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
        @media (max-width: 576px) {
            max-width: 280px;
            min-height: 280px;
        }
        @media (max-width: 355px) {
            max-width: 180px;
            min-height: 180px;
        }
    }
}
.swiper-container {
    max-width: 1170px;
    @media (max-width: 1850px) {
        max-width: 955px;
    }
    @media (max-width: 1340px) {
        max-width: 700px;
    }
    @media (max-width: 1100px) {
        max-width: 600px;
    }
}
.swiper-container-mob {
    max-width: 349px;
    @media (max-width: 576px) {
        max-width: 280px;
    }
    @media (max-width: 355px) {
        max-width: 180px;
    }
}
.swiper-container-acc {
    max-width: 1740px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 1;
    @media (max-width: 1540px) {
        max-width: 970px;
    }
    @media (max-width: 992px) {
        position: relative;
        max-width: 490px;
    }
}
.box__wrapper .swiper-button-next {
    position: absolute;
    right: 0px;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    background-color: #fff;
    background-image: url("../img/decorations/right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 30px 30px;
    background-position: center;
    &::after {
        display: none;
    }
    @media (max-width: 992px) {
        bottom: 20px;
        right: 50%;
        top: auto;
        transform: translate(50%, 50%);
        z-index: 9;
        width: 35px;
        height: 35px;
        background-size: 20px 20px;
    }
}
.accelerator .swiper-button-next {
    position: absolute;
    right: 2%;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    background-color: var(--color-gray);
    background-image: url("../img/decorations/right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 30px 30px;
    background-position: center;
    &::after {
        display: none;
    }
    @media (max-width: 992px) {
        bottom: 0px;
        left: 0;
        right: auto;
        top: auto;
        z-index: 9;
        width: 35px;
        height: 35px;
        background-size: 20px 20px;
    }
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
    width: 180px;
    height: 60px;
    margin: 0 auto;
    color: #fff;
    border: 4px solid transparent;
    background-color: var(--color-black);
    transition: all 0.5s ease;
    @media (max-width: 1540px) {
        font-size: 14px;
    }
    @media (min-width: 992px) {
        &:hover {
            background-color: #fff;
            color: var(--color-black);
            border: 4px solid var(--color-black);
            transition: all 0.5s ease;
        }
    }
    @media (max-width: 576px) {
        padding: 0.9em 3.5em;
    }
}
.bottom {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    margin-top: 25px;
    @media (max-width: 1540px) {
        margin-top: 10px;
    }
    @media (max-width: 576px) {
        margin-top: 0;
    }
}
body .active {
    display: block;
}
.section__title {
    display: none;
    @media (max-width: 992px) {
        display: block;
        padding-top: 10px;
        margin: 0 auto 10px 0;
        font-family: "Simpler", sans-serif;
        font-weight: 700;
        font-size: 30px;
    }
}
.social {
    position: absolute;
    top: 10px;
    right: 0;
    @media (max-width: 1540px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    @media (max-width: 992px) {
        position: relative;
        align-items: flex-start;
        padding:0 20px;
    }
    @media (max-width: 768px) {
        position: relative;
        flex-direction: column;
    }
    &__item {
        width: 25px;
        height: 25px;
        @media (max-width: 992px) {
            width: 30px;
            height: 30px;
        }
        @media (max-width: 576px) {
            width: 25px;
            height: 25px;
        }
        &:not(:last-child) {
            margin-bottom: 30px;
            @media (max-width: 1540px) {
                margin-bottom: 0;
                margin-right: 25px;
            }
            @media (max-width: 768px) {
                margin-right: 0;
                margin-bottom: 25px;
            }
            @media (max-width: 576px) {
                margin-right: 0;
                margin-bottom: 20px;
            }
        }
        svg {
            width: 100%;
            height: 100%;
            object-fit: cover;
            fill: #000;
            transition: fill 0.3s ease;
            @media (max-width: 992px) {
                fill: var(--color-white);
            }
            @media (min-width: 992px) {
                &:hover {
                    fill: #707070;
                    transition: fill 0.3s ease;
                }
            }
        }
    }
}
.thanks {
    text-align: center;
    padding-top: 10px;
    @include adaptiv-font(24, 13);
}
.js-validate-error-label {
    margin-top: -20px;
    margin-bottom: 7px;
    @media (max-width: 380px) {
        margin-top: 0;
    }
}
.main--visible {
    @media (min-width: 1400px) {
        position: relative;
        z-index: 3000;
        color: var(--color-fff);
    }
}