.deals {
    animation: showOtherPages 1.5s;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-gray);
    &__wrapper {
        padding: 10px 0;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        max-width: 950px;
        margin: 0 auto;
        color: var(--color-middlegray);
        @media (max-width: 768px) {
            flex-direction: column;
        }
    }
    &__item {
        position: relative;
        background-color: var(--color-fff);
        padding: 15px 10px;
    }

    &__title {
        font-size: 23px;
        text-transform: uppercase;
    }
    &__left {
        margin-right: 50px;
        flex: 0 1 35%;
        @media (max-width: 768px) {
            flex: 0 1 100%;
            margin-right: 0;
        }
        &-title {
            text-transform: uppercase;
            font-size: 18px;
        }
        &-text {
            p {
                margin: 12px 0;
            }
        }
    }
    &__link {
        text-align: right;
        a {
            display: inline-block;
            position: relative;
            font-size: 25px;
            text-transform: uppercase;
            color: var(--color-middlegray);
            padding-right: 25px;
            transition: color 0.5s ease;
            &:hover {
                color: var(--color-middlegray-hover);
                transition: color 0.5s ease;
            }
            &::after {
                content: '';
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                width: 20px;
                height: 20px;
                background-image: url('../img/decorations/right-arrow-next.svg');
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center;
            }
        }
    }
    &__right {
        flex: 0 1 65%;
        @media (max-width: 768px) {
            flex: 0 1 100%;
            margin-top: 15px;
        }
        p {
            margin: 5px 200px 0 0;
            @media (max-width: 576px) {
                margin: 5px 0 0 0;
            }
        }
    }
    &__btn {
        position: absolute;
        bottom: 15px;
        right: 10px;
       a {
            margin: 0 0 0 auto;
            @media (max-width: 576px) {
                margin: 0 auto;
            }
        }
        @media (max-width: 576px) {
            position: static;
            padding-top: 10px;
        }
    }
    &__partner {
        padding-top: 10px;
    }
