.main {
    padding-top: 10px;
    @media (min-width: 992px) {
        animation: showOtherPages 1.5s;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        @media (max-width: 992px) {
            flex-direction: column;
        }
    }
    &__offer {
        max-width: 800px;
    }
    &__title {
        @include adaptiv-font(120, 60);
        font-family: "Tangerine", "Gilroy", sans-serif;
        font-weight: 700;
    }

    &__subtitle {
        @include adaptiv-font(40, 18);
        font-family: "Simpler", "Gilroy", sans-serif;
        margin-bottom: 30px;
        text-transform: uppercase;
        @media (max-width: 768px) {
            margin-bottom: 15px;
        }
        @media (max-width: 370px) {
            margin-bottom: 5px;
        }
    }

    &__text {
        @include adaptiv-font(20, 13);
        font-weight: 300;
        font-family: "Simpler", "Gilroy", sans-serif;
        @media (max-width: 768px) {
            line-height: 150%;
        }
    }
    &__burger {
        display: none;
        @media (max-width: 992px) {
            &_nav {
                right: 15px;
                top: 15px;
            }
            display: block;
            position: absolute;
            top: 5px;
            right: 0;
            width: 25px;
            height: 25px;
            z-index: 100;
            background-color: #fff;
            span {
                display: block;
                position: absolute;
                width: 100%;
                height: 5px;
                background-color: var(--color-black);
                top: 50%;
                transform: translateY(-50%);
                transition: all 0.3s ease;
            }
            &::before,
            &::after {
                content: "";
                display: block;
                position: absolute;
                width: 100%;
                height: 5px;
                background-color: var(--color-black);
                left: 0;
                transition: all 0.3s ease;
            }
            &::before {
                top: 0;
            }
            &::after {
                bottom: 0;
            }
            &_active {
                background-color: var(--color-black);
                span,
                &::after,
                &::before {
                    background-color: var(--color-white);
                }

                span {
                    transform: scale(0);
                }
                &::after {
                    transform: rotate(-45deg);
                    bottom: 11px;
                }
                &::before {
                    transform: rotate(45deg);
                    top: 9px;
                }
            }
        }
    }
}
