.accelerator {
    padding-top: 10px;
    animation: showOtherPages 1.5s;
    &__item {
        position: relative;
        max-height: 700px;
        min-height: 200px;
        display: flex;
        @media (max-width: 1540px) {
            max-height: 590px;
        }
        @media (max-width: 576px) {
            max-height: 530px;
        }
        @media (max-width: 355px) {
            max-height: 430px;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 0 0;
        }

    }
    &__photo {
        position: relative;
    }
    &__item-wrapper {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 20px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: var(--color-white);
        background-color: rgba(0, 0, 0, 0.7);
        text-align: center;
        @media (max-width: 768px) {
            padding: 10px 0;
        }
    }
    &__name {
        @include adaptiv-font(40, 25);
    }
    &__position {
        @include adaptiv-font(20, 13);
    }
    &__works {
       display: flex;
       flex-direction: column;
       justify-content: flex-start;
       align-items: center;
       margin-right: 20px;
       @media (max-width: 768px) {
            margin-right: 10px;
       }
    }
    &__works-item {
        max-width: 106px;
        cursor: pointer;
        &:not(:last-child) {
            margin-bottom: 20px;
            @media (max-width: 768px) {
                margin-bottom: 10px;
            }
        }
        // @media (max-width: 1540px) {
        //     max-width: 75px;
        // }
        // @media (max-width: 576px) {
        //     max-width: 60px;
        // }
        // @media (max-width: 370px) {
        //     max-width: 50px;
        // }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
